.mobile-order_sidebar {
  display: none;
  padding: 20px;
  background-color: #fff;
  box-shadow: 0 0 16px 0 rgba(0, 0, 0, 0.16);
  position: fixed;
  width: 100%;
  bottom: 0;
  left: 0;
  z-index: 99;
  transform: translateY(440px);
  transition: .7s all ease;
}

.mobile-order_sidebar.active {
  transform: translateY(0);
}

.mobile-order_sidebar .mobile-order_top {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding-bottom: 20px;
  margin-bottom: 20px;
  border-bottom: 2px solid #E9E9E9;
}

.mobile-order_sidebar .order-top_left {
  padding-left: 60px;
  padding-right: 15px;
  width: calc(100% - 115px);
  display: flex;
  justify-content: space-between;
  align-items: center;
  flex-wrap: wrap;
  border-right: 2px solid #E9E9E9;
  position: relative;
}

.mobile-order_sidebar .order-top_left.active:before {
  transform: rotate(-90deg);
}

.mobile-order_sidebar .order-top_left:before {
  display: block;
  content: '';
  width: 44px;
  height: 44px;
  border-radius: 12px;
  background: #F6F6F6 url("../img/arrow-left-blue.svg") center no-repeat;
  background-size: 80%;
  position: absolute;
  left: 0;
  top: 0;
  transform: rotate(90deg);
  transition: .7s all ease;
}

.mobile-order_sidebar .order-top_left .top-left_row {
  display: flex;
  justify-content: space-between;
  align-items: center;
  width: 100%;
}

.mobile-order_sidebar .order-top_left .top-left_row p {
  font-size: 16px;
  font-weight: 500;
  line-height: 23px;
}

.mobile-order_sidebar .order-top_left .top-left_row .tr1 {
  color: #1F283C;
  margin-bottom: 2px;
}

.mobile-order_sidebar .order-top_left .top-left_row .tr2 {
  color: #23A8CD;
  margin-bottom: 2px;
}

.mobile-order_sidebar .order-top_left .top-left_row .tr3 {
  font-size: 14px;
  font-weight: 400;
  line-height: 20px;
}

.mobile-order_sidebar .order-top_left .top-left_row .tr4 {
  font-size: 14px;
  font-weight: 400;
  line-height: 20px;
}

.mobile-order_sidebar .to-checkout_button {
  display: block;
  width: 100px;
  font-size: 15px;
  font-weight: 500;
  background: #23A8CD;
  text-align: center;
  line-height: 20px;
  padding: 12px 0;
  border-radius: 12px;
  color: #fff;
  box-shadow: 0 8px 24px 0 rgba(24, 120, 147, 0.24);
}

.mobile-order_sidebar .to-checkout_button:nth-child(2) {
  margin-top: 5px;
}

.mobile-order_sidebar .to-checkout_button.disabled {
  background: #a6aebe;
}

.mobile-order_sidebar .details-title {
  font-size: 14px;
  line-height: 20px;
  margin-bottom: 7px;
}

.mobile-order_sidebar .mobile-order_details--wrap {
  padding: 13px 17px;
  border: 2px solid #E9E9E9;
  border-radius: 4px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 20px;
}

.mobile-order_sidebar .mobile-order_details--wrap .details-left {
  width: calc(100% - 70px);
}

.mobile-order_sidebar .mobile-order_details--wrap .details-left p {
  line-height: 23px;
  color: #1F283C;
  font-weight: 500;
  margin-bottom: 8px;
}

.mobile-order_sidebar .mobile-order_details--wrap .details-left p:last-child {
  margin-bottom: 0;
}

.mobile-order_sidebar .mobile-order_details--wrap .details-left p span {
  color: #797C84;
}

.mobile-order_sidebar .mobile-order_details--wrap .edit-order {
  display: block;
  width: 66px;
  border-radius: 12px;
  background-color: #F6F6F6;
  padding: 12px 0;
  font-size: 15px;
  font-weight: 500;
  text-align: center;
  color: #23A8CD;
  line-height: 20px;
}

.mobile-order_sidebar .order-details_bottom {
  padding-bottom: 20px;
  margin-bottom: 20px;
  border-bottom: 2px solid #E9E9E9;
}

.mobile-order_sidebar .details-bottom_row {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 8px;
}

.mobile-order_sidebar .details-bottom_row:last-child {
  margin-bottom: 0;
}

.mobile-order_sidebar .details-bottom_row:last-child p:first-child {
  font-weight: 500;
  color: #1F283C;
}

.mobile-order_sidebar .details-bottom_row:last-child p:nth-child(2) {
  color: #23A8CD;
}

.mobile-order_sidebar .details-bottom_row p {
  line-height: 23px;
}

.mobile-order_sidebar .details-bottom_row p:nth-child(2) {
  font-weight: 500;
  color: #1F283C;
}

.mobile-order_sidebar .bottom-details {
  font-size: 13px;
  line-height: 21px;
  text-align: center;
}

.mobile_charter-type--dropdown {
  display: block;
  position: relative;
  top: 0;
  padding: 0;
  box-shadow: none;
}

.mobile_charter-type--dropdown .chooser-item {
  margin-bottom: 20px;
}

@media screen and (max-width: 500px) {
  .mobile-order_sidebar {
    display: block;
  }
}

@media screen and (max-width: 350px) {
  .mobile-order_sidebar {
    transform: translateY(465px);
  }
  .mobile-order_sidebar .order-top_left {
    width: calc(100% - 95px);
  }
  .mobile-order_sidebar .order-top_left .top-left_row p {
    font-size: 14px;
  }
  .mobile-order_sidebar .to-checkout_button {
    width: 80px;
  }
}

